.center {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: 100vh;

  .ring {
    position: absolute;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    animation: ring 2s linear infinite;

    &:before {
      position: absolute;
      content: '';
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      border-radius: 50%;
      box-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
    }
  }

  span {
    color: #737373;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 200px;
    animation: text 3s ease-in-out infinite;
  }
}

@keyframes ring {
  0% {
    transform: rotate(0deg);
    box-shadow: 1px 5px 2px #e65c00;
  }

  25% {
    transform: rotate(90deg);
    box-shadow: 1px 5px 2px #e52d27;
  }

  50% {
    transform: rotate(180deg);
    box-shadow: 1px 5px 2px #F67280;
  }

  75% {
    transform: rotate(270deg);
    box-shadow: 1px 5px 2px #bd081c;
  }

  100% {
    transform: rotate(360deg);
    box-shadow: 1px 5px 2px #0456c8;
  }
}

@keyframes text {
  50% {
    color: black;
  }
}
